.hero-section {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title {
    font-size: 16vw;
    span {
        color: map-get($map: $colors, $key: white);
        -webkit-text-stroke: 1.3px map-get($map: $colors, $key: black);
    }
}

@media(min-width: map-get($map: $breakpoints, $key: md)) {
    .title {
        font-size: 7rem;
    }
}

.laptop {
    width: 80%;
    max-width: 500px;
}